//@ts-nocheck
import { connectComet } from "@cosmjs/tendermint-rpc";
import { QueryClient } from "@cosmjs/stargate";
export const createRPCQueryClient = async ({ rpcEndpoint }) => {
    const tmClient = await connectComet(rpcEndpoint);
    const client = new QueryClient(tmClient);
    return {
        cosmos: {
            auth: {
                v1beta1: (await import("../cosmos/auth/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            authz: {
                v1beta1: (await import("../cosmos/authz/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            bank: {
                v1beta1: (await import("../cosmos/bank/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            distribution: {
                v1beta1: (await import("../cosmos/distribution/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            gov: {
                v1: (await import("../cosmos/gov/v1/query.rpc.Query")).createRpcQueryExtension(client),
                v1beta1: (await import("../cosmos/gov/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            mint: {
                v1beta1: (await import("../cosmos/mint/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            params: {
                v1beta1: (await import("../cosmos/params/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            staking: {
                v1beta1: (await import("../cosmos/staking/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            tx: {
                v1beta1: (await import("../cosmos/tx/v1beta1/service.rpc.Service")).createRpcQueryExtension(client)
            },
            upgrade: {
                v1beta1: (await import("../cosmos/upgrade/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            }
        },
        cyber: {
            bandwidth: {
                v1beta1: (await import("./bandwidth/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            clock: {
                v1: (await import("./clock/v1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            dmn: {
                v1beta1: (await import("./dmn/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            graph: {
                v1beta1: (await import("./graph/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            grid: {
                v1beta1: (await import("./grid/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            liquidity: {
                v1beta1: (await import("./liquidity/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            rank: {
                v1beta1: (await import("./rank/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            },
            resources: {
                v1beta1: (await import("./resources/v1beta1/query.rpc.Query")).createRpcQueryExtension(client)
            }
        }
    };
};
