import { MsgInvestmint, MsgUpdateParams } from "./tx";
export const registry = [["/cyber.resources.v1beta1.MsgInvestmint", MsgInvestmint], ["/cyber.resources.v1beta1.MsgUpdateParams", MsgUpdateParams]];
export const load = (protoRegistry) => {
    registry.forEach(([typeUrl, mod]) => {
        protoRegistry.register(typeUrl, mod);
    });
};
export const MessageComposer = {
    encoded: {
        investmint(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgInvestmint",
                value: MsgInvestmint.encode(value).finish()
            };
        },
        updateParams(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgUpdateParams",
                value: MsgUpdateParams.encode(value).finish()
            };
        }
    },
    withTypeUrl: {
        investmint(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgInvestmint",
                value
            };
        },
        updateParams(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgUpdateParams",
                value
            };
        }
    },
    toJSON: {
        investmint(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgInvestmint",
                value: MsgInvestmint.toJSON(value)
            };
        },
        updateParams(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgUpdateParams",
                value: MsgUpdateParams.toJSON(value)
            };
        }
    },
    fromJSON: {
        investmint(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgInvestmint",
                value: MsgInvestmint.fromJSON(value)
            };
        },
        updateParams(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgUpdateParams",
                value: MsgUpdateParams.fromJSON(value)
            };
        }
    },
    fromPartial: {
        investmint(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgInvestmint",
                value: MsgInvestmint.fromPartial(value)
            };
        },
        updateParams(value) {
            return {
                typeUrl: "/cyber.resources.v1beta1.MsgUpdateParams",
                value: MsgUpdateParams.fromPartial(value)
            };
        }
    }
};
