import { MsgCyberlink } from "./tx";
export const registry = [["/cyber.graph.v1beta1.MsgCyberlink", MsgCyberlink]];
export const load = (protoRegistry) => {
    registry.forEach(([typeUrl, mod]) => {
        protoRegistry.register(typeUrl, mod);
    });
};
export const MessageComposer = {
    encoded: {
        cyberlink(value) {
            return {
                typeUrl: "/cyber.graph.v1beta1.MsgCyberlink",
                value: MsgCyberlink.encode(value).finish()
            };
        }
    },
    withTypeUrl: {
        cyberlink(value) {
            return {
                typeUrl: "/cyber.graph.v1beta1.MsgCyberlink",
                value
            };
        }
    },
    toJSON: {
        cyberlink(value) {
            return {
                typeUrl: "/cyber.graph.v1beta1.MsgCyberlink",
                value: MsgCyberlink.toJSON(value)
            };
        }
    },
    fromJSON: {
        cyberlink(value) {
            return {
                typeUrl: "/cyber.graph.v1beta1.MsgCyberlink",
                value: MsgCyberlink.fromJSON(value)
            };
        }
    },
    fromPartial: {
        cyberlink(value) {
            return {
                typeUrl: "/cyber.graph.v1beta1.MsgCyberlink",
                value: MsgCyberlink.fromPartial(value)
            };
        }
    }
};
